const request = indexedDB.open('myIndexDb', 1);


request.onerror = function (event) {
    // 
    console.error('IndexedDB ', event);
}

let db: IDBDatabase | null = null;

request.onsuccess = function (event) {

    db = (event.target as IDBOpenDBRequest).result;

    console.log('IndexedDB ');
}

request.onupgradeneeded = function (event) {

    db = (event.target as IDBOpenDBRequest).result;

    if (!db.objectStoreNames.contains('myObjectStore')) {
        db.createObjectStore('myObjectStore', { keyPath: 'id' });
    }

    console.log('IndexedDB ');
}

export function addData(data: any) {
    if (!db) {
        console.error('IndexedDB ');
        return;
    }

    const transaction = db.transaction(['myObjectStore'], 'readwrite');

    const objectStore = transaction.objectStore('myObjectStore');

    objectStore.add(data);

    transaction.oncomplete = function () {
        console.log('');
    }

    transaction.onerror = function (event) {
        console.error('', event);
    }
}

export async function getData(id: number) {
    return new Promise((resolve, reject) => {

        if (!db) {
            console.error('IndexedDB ');
            reject('IndexedDB ');
            return
        }

        const transaction = db.transaction(['myObjectStore'], 'readonly');

        const objectStore = transaction.objectStore('myObjectStore');

        const request = objectStore.get(id);

        request.onsuccess = function (event) {
            const data = (event.target as IDBRequest).result;
            console.log('', data);
            resolve(data);
        }

        request.onerror = function (event) {
            console.error('', event);
            reject(event)
        }
    })
}

export function deleteData(id: number) {
    if (!db) {
        console.error('IndexedDB ');
        return;
    }

    const transaction = db.transaction(['myObjectStore'], 'readwrite');

    const objectStore = transaction.objectStore('myObjectStore');
    
    const request = objectStore.delete(id);
    
    transaction.oncomplete = function () {
        console.log('');
    }

    transaction.onerror = function (event) {
        console.error('', event);
    }
}

export function clearData() {
    if (!db) {
        console.error('IndexedDB ');
        return;
    }

    const transaction = db.transaction(['myObjectStore'], 'readwrite');

    const objectStore = transaction.objectStore('myObjectStore');
    
    const request = objectStore.clear();
    
    transaction.oncomplete = function () {
        console.log('');
    }
    
    transaction.onerror = function (event) {
        console.error('', event);
    }
}

export function closeDb() {
    if (db) {
        db.close();
    }
    console.log('IndexedDB ');
}

export function getAllData() {
    if (!db) {
        console.error('IndexedDB ');
        return;
    }

    const transaction = db.transaction(['myObjectStore'], 'readonly');

    const objectStore = transaction.objectStore('myObjectStore');
        
    const request = objectStore.getAll();
    
    request.onsuccess = function (event) {
        const data = (event.target as IDBRequest).result;
        console.log('', data);
    }
    
    request.onerror = function (event) {
        console.error('', event);
    }

}

export function updateData(id: number, newData: any) {
    if (!db) {
        console.error('IndexedDB ');
        return;
    }

    const transaction = db.transaction(['myObjectStore'], 'readwrite');

    const objectStore = transaction.objectStore('myObjectStore');
    
    const getRequest = objectStore.get(id);
    
    getRequest.onsuccess = function(event) {
        const data = (event.target as IDBRequest).result;
        if (data) {
          const updateRequest = objectStore.put(newData);
          updateRequest.onsuccess = function(event) {
            console.log('Data updated successfully');
          };
        } else {
          console.log('Data with ID 887 not found');
        }
    }
}
